import React, { Component } from "react";
import Loading from "./Loading";
import NewsItems from "./NewsItems";
import PropTypes from "prop-types";
import InfiniteScroll from "react-infinite-scroll-component";

export class NewsComponent extends Component {
  static defaultProps = {
    country: "us",
    pageSize: 12,
    category: "sport",
  };

  PropTypes = {
    country: PropTypes.string,
    pageSize: PropTypes.number,
    category: PropTypes.string,
  };

  capitalizeFirstLetter = (string) => {
    return string.charAt(0).toUpperCase() + string.slice(1);
  };

  constructor(props) {
    super(props);
    this.state = {
      articles: [],
      loading: true,
      page: 1,
      totalResults: 0,
    };
    document.title = `${this.capitalizeFirstLetter(
      this.props.category
    )} - NewsPaper`;
  }

  newsUpdate = async () => {
    this.props.setProgress(10);
    const url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=
${this.props.apikey}&page=${this.state.page}&pageSize=${this.props.pageSize}`;
    this.setState({ loading: true });
    let data = await fetch(url);
    this.props.setProgress(30);
    let parseData = await data.json();
    this.props.setProgress(70);
    this.setState({
      articles: parseData.articles,
      totalResults: parseData.totalResults,
      loading: false,
    });
    this.props.setProgress(100);
  };

  async componentDidMount() {
    this.newsUpdate();
  }

  handelerPreviousBtn = async () => {
    this.setState({
      page: this.state.page - 1,
    });

    this.newsUpdate();
  };

  handelerNextBtn = async () => {
    this.setState({
      page: this.state.page + 1,
    });
    this.newsUpdate();
  };

  fetchMoreData = async () => {
    this.setState({
      page: this.state.page + 1,
    });

    const url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&country=${this.props.country}
    &category=${this.props.category}&apiKey=${this.props.apikey}&page=${this.state.page}&pageSize=${this.props.pageSize}`;

    let data = await fetch(url);
    let parsedData = await data.json();
    console.log(parsedData);
    this.setState({
      articles: this.state.articles.concat(parsedData.articles),
      totalResults: parsedData.totalResults,
    });
  };

  render() {
    return (
      <div>
        <>
          <h1
            className="text-center"
            style={{ marginTop: "50px", paddingTop: "20px", fontSize: "40px" }}
          >
            NewsPapers - Top Headlines
          </h1>

          <InfiniteScroll
            dataLength={this.state.articles.length}
            next={this.fetchMoreData}
            hasMore={this.state.articles.length !== this.state.totalResults}
            loader={<Loading />}
          >
            <div className="container">
              <div className="row">
                {this.state.articles.map((e) => {
                  return (
                    <div className="col-md-4" key={e.url}>
                      <NewsItems
                        title={e.title}
                        describtion={e.description}
                        newsImg={e.urlToImage}
                        NewsUrl={e.url}
                        name={e.source.name}
                        author={e.author}
                        date={e.publishedAt}
                      />
                    </div>
                  );
                })}
              </div>
            </div>
          </InfiniteScroll>
        </>
      </div>
    );
  }
}

export default NewsComponent;
