import React from "react";

// export class NewsItems extends Component {
const NewsItems = (props) => {
  let { title, describtion, newsImg, NewsUrl, name, author, date } = props;
  return (
    <div >
      <div className="card my-4 " style ={{ border : '2px solid #5bc0de', borderRadius : '10px' }}>
        <img
          style={{ height: "12rem", borderRadius : '10px'}}
          src={
            !newsImg
              ? "https://images.pexels.com/photos/3626734/pexels-photo-3626734.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500"
              : newsImg
          }
          className="card-img-top"
          alt="..."
        />
        <div className="card-body">
          <p className="card-name">{name}</p>
          <h5 className="card-title">{title}</h5>
          <p className="card-text">{describtion}</p>
          <p className="card-text">
            <small className="text-muted">
              By {author ? author : "Unknown"}, on{" "}
              {new Date(date).toGMTString()} ago
            </small>
          </p>
          <a
            href={NewsUrl}
            target="_blank"
            rel="noreferrer"
            className="btn btn-sm text-light btn-info"
          >
            Read More
          </a>
        </div>
      </div>
    </div>
  );
};

export default NewsItems;
